.hobbies-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 2rem;
  height: calc(80vh);
  overflow-y: auto;
}

.hobbies-container::-webkit-scrollbar {
  width: 8px;
}

.hobbies-container::-webkit-scrollbar-track {
  background: var(--primary-bg);
}

.hobbies-container::-webkit-scrollbar-thumb {
  background: var(--primary-bg);
  border-radius: 4px;
}

.hobbies-container::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.hobby-section {
  margin-bottom: 40px;
  padding: 25px;
  border-radius: 12px;
  background-color: var(--primary-bg);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.hobby-section:hover {
  transform: translateY(-5px);
}

.hobby-header {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
  justify-content: center;
}

.hobby-icon {
  font-size: 2rem;
  color: var(--primary-text);
  margin-right: 15px;
}

.hobby-section h2 {
  color: var(--primary-text);
  margin: 0;
  font-size: 1.8rem;
  text-align: center;
}

/* Movie Section */


.letterboxd-profile-link {
  text-decoration: none;
  color: var(--primary-text);
  font-weight: bold;
  transition: color 0.3s ease;
}

.letterboxd-profile-link:hover {
  color: #666;
}

.letterboxd-showcase {
  display: flex;
  flex-direction: column;
  background-color: var(--primary-bg);
}

.letterboxd-link {
  display: flex;
  align-items: center;
  gap: 10px;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  text-decoration: none;
}

.movie-grid {
  display: grid;
  grid-template-columns: repeat(4, 150px);
  gap: 20px;
  justify-content: center;
}

.movie-card {
  margin: 0.5rem;
  position: relative;
  aspect-ratio: 2/3;
  overflow: hidden;
  border-radius: 8px;
  transition: transform 0.3s ease;
}

.movie-card:hover {
  transform: scale(1.1);
}

.movie-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.movie-card span {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 8px;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  text-align: center;
}

/* Sports Section */
.sports-showcase {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--primary-bg);
}

.team-card {
  background-color: --accent-color;
  margin: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 8px;
  transition: transform 0.3s ease;
}

.team-card:hover {
  transform: translateY(-5px);
  cursor: pointer;
}

.team-card img {
  width: 120px;
  height: 120px;
  object-fit: contain;
  margin-bottom: 10px;
}

.cricket-icon {
  font-size: 3rem;
  margin-bottom: 10px;
}

/* Educational Content Section */
.channel-grid {
  display: grid;
  grid-template-columns: repeat(2, 250px);
  gap: 20px;
  justify-content: center;
}

.channel-card {
  padding: 15px;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  transition: transform 0.3s ease;
}

.channel-card:hover {
  transform: translateY(-5px);
}

.channel-card h3 {
  margin: 0 0 5px 0;
  color: var(--primary-text);
}

.channel-topic {
  color: var(--primary-text);
  font-size: 0.9rem;
}

@media (max-width: 1000px) {
  .sports-showcase,
  .channel-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 15px;
  }
}

.letterboxd-profile {
  background: var(--primary-bg);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
}

.letterboxd-header {
  display: flex;
  align-items: center;
  font-size: large;
  gap: 8px;
  justify-content: center;
}

.chelsea-slogan {
  color: #034694;
  font-size: 1.2rem;
}

.chelsea-hashtag {
  font-weight: bold;
  color: #034694;
  text-decoration: none;
  transition: color 0.3s ease;
}

.chelsea-hashtag:hover {
  color: #022c5c;
}

@media (max-width: 768px) {
  .movie-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }

  .hobby-section {
    padding: 15px;
  }

  .hobby-icon {
    font-size: 1.5rem;
  }

  .hobby-section h2 {
    font-size: 1.4rem;
  }

  .channel-grid {
    grid-template-columns: 1fr;
  }
}
